<template>
    <div class="vue-doc-wrapper">
        
        <div class="vue-doc-title">
          <h2 id="vue-doc-全选1" class="vue-doc-element">全选</h2>
<p class="vue-doc-element">null</p>

        </div>
        <div class="vue-doc-title-EN">
          <h2 id="vue-doc-buttonsize1" class="vue-doc-element">Button Size</h2>
<p class="vue-doc-element">Button</p>

        </div>

        


        <div class="vue-doc-content">
          <p class="vue-doc-element">在实现全选效果时，你可能会用到 indeterminate 属性。示例代码只是一种写法，业务中可以用更多的方法，比如计算属性。</p>

        </div>
        <div class="vue-doc-content-EN">
          
        </div>
        
          <div class="vue-doc-demo">
            <template>
    <div style="border-bottom: 1px solid #e9e9e9;padding-bottom:6px;margin-bottom:6px;">
        <Checkbox
            :indeterminate="indeterminate"
            :value="checkAll"
            @click.prevent.native="handleCheckAll">全选</Checkbox>
    </div>
    <CheckboxGroup v-model="checkAllGroup" @on-change="checkAllGroupChange">
        <Checkbox label="香蕉"></Checkbox>
        <Checkbox label="苹果"></Checkbox>
        <Checkbox label="西瓜"></Checkbox>
    </CheckboxGroup>
</template>
          </div>
            <div class="vue-doc-code" :class="{'vue-doc-code-show':showVueDocCode}">
              <div class="vue-doc-code-copy" @click="vueDocCodeCopy">{{vueDocCodeCopyText}}</div>
              <div class="vue-doc-code-content" ref="vueDocCodeBox">
                <pre class="vue-doc-element"><code class="lang-vue">&lt;template&gt;
    &lt;div style=<span class="hljs-string">"border-bottom: 1px solid #e9e9e9;padding-bottom:6px;margin-bottom:6px;"</span>&gt;
        &lt;Checkbox
            :indeterminate=<span class="hljs-string">"indeterminate"</span>
            :value=<span class="hljs-string">"checkAll"</span>
            <span class="hljs-meta">@click</span>.prevent.<span class="hljs-keyword">native</span>=<span class="hljs-string">"handleCheckAll"</span>&gt;全选&lt;/Checkbox&gt;
    &lt;/div&gt;
    &lt;CheckboxGroup v-model=<span class="hljs-string">"checkAllGroup"</span> <span class="hljs-meta">@on</span>-change=<span class="hljs-string">"checkAllGroupChange"</span>&gt;
        &lt;Checkbox label=<span class="hljs-string">"香蕉"</span>&gt;&lt;/Checkbox&gt;
        &lt;Checkbox label=<span class="hljs-string">"苹果"</span>&gt;&lt;/Checkbox&gt;
        &lt;Checkbox label=<span class="hljs-string">"西瓜"</span>&gt;&lt;/Checkbox&gt;
    &lt;/CheckboxGroup&gt;
&lt;/template&gt;
&lt;script&gt;
    export <span class="hljs-keyword">default</span> {
        <span class="hljs-keyword">data</span> () {
            <span class="hljs-keyword">return</span> {
                indeterminate: <span class="hljs-literal">true</span>,
                checkAll: <span class="hljs-literal">false</span>,
                checkAllGroup: [<span class="hljs-string">'香蕉'</span>, <span class="hljs-string">'西瓜'</span>]
            }
        },
        methods: {
            handleCheckAll () {
                <span class="hljs-keyword">if</span> (<span class="hljs-keyword">this</span>.indeterminate) {
                    <span class="hljs-keyword">this</span>.checkAll = <span class="hljs-literal">false</span>;
                } <span class="hljs-keyword">else</span> {
                    <span class="hljs-keyword">this</span>.checkAll = !<span class="hljs-keyword">this</span>.checkAll;
                }
                <span class="hljs-keyword">this</span>.indeterminate = <span class="hljs-literal">false</span>;

                <span class="hljs-keyword">if</span> (<span class="hljs-keyword">this</span>.checkAll) {
                    <span class="hljs-keyword">this</span>.checkAllGroup = [<span class="hljs-string">'香蕉'</span>, <span class="hljs-string">'苹果'</span>, <span class="hljs-string">'西瓜'</span>];
                } <span class="hljs-keyword">else</span> {
                    <span class="hljs-keyword">this</span>.checkAllGroup = [];
                }
            },
            checkAllGroupChange (<span class="hljs-keyword">data</span>) {
                <span class="hljs-keyword">if</span> (<span class="hljs-keyword">data</span>.length === <span class="hljs-number">3</span>) {
                    <span class="hljs-keyword">this</span>.indeterminate = <span class="hljs-literal">false</span>;
                    <span class="hljs-keyword">this</span>.checkAll = <span class="hljs-literal">true</span>;
                } <span class="hljs-keyword">else</span> <span class="hljs-keyword">if</span> (<span class="hljs-keyword">data</span>.length &gt; <span class="hljs-number">0</span>) {
                    <span class="hljs-keyword">this</span>.indeterminate = <span class="hljs-literal">true</span>;
                    <span class="hljs-keyword">this</span>.checkAll = <span class="hljs-literal">false</span>;
                } <span class="hljs-keyword">else</span> {
                    <span class="hljs-keyword">this</span>.indeterminate = <span class="hljs-literal">false</span>;
                    <span class="hljs-keyword">this</span>.checkAll = <span class="hljs-literal">false</span>;
                }
            }
        }
    }
&lt;/script&gt;



</code></pre>

              </div>
              <div class="vue-doc-code-toggle" @click="vueDocCodeToggle">
                <span class="vue-doc-code-toggle-text"></>{{showVueDocCode?"隐藏代码":"显示代码"}}</span>
                <span class="vue-doc-code-toggle-text-EN"></>{{showVueDocCode?"Hide Code":"Show Code"}}</span>
              </div>
            </div>
          


    </div>
  </template>
  <script>
    
  
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      
    }
  };
  export default { mixins: [mixin],
        data () {
            return {
                indeterminate: true,
                checkAll: false,
                checkAllGroup: ['香蕉', '西瓜']
            }
        },
        methods: {
            handleCheckAll () {
                if (this.indeterminate) {
                    this.checkAll = false;
                } else {
                    this.checkAll = !this.checkAll;
                }
                this.indeterminate = false;

                if (this.checkAll) {
                    this.checkAllGroup = ['香蕉', '苹果', '西瓜'];
                } else {
                    this.checkAllGroup = [];
                }
            },
            checkAllGroupChange (data) {
                if (data.length === 3) {
                    this.indeterminate = false;
                    this.checkAll = true;
                } else if (data.length > 0) {
                    this.indeterminate = true;
                    this.checkAll = false;
                } else {
                    this.indeterminate = false;
                    this.checkAll = false;
                }
            }
        }
    }
</script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


